	module.exports = {"data":{"postsJson":{"title":"Betrayed, Arrested & Denied","questionsTitle":"FOLLOWING GODLY EXAMPLES","display":"block","bookName":"Luke","ref":"22:1-22:71","html":"<p>   <strong>22</strong><u>1</u>Now the feast of unleavened bread, which is called the Passover, was approaching. <u>2</u>The chief priests and the scribes sought how they might put him to death, for they feared the people. <u>3</u>Satan entered into Judas, who was also called Iscariot, who was numbered with the twelve. <u>4</u>He went away, and talked with the chief priests and captains about how he might deliver him to them. <u>5</u>They were glad, and agreed to give him money. <u>6</u>He consented, and sought an opportunity to deliver him to them in the absence of the multitude. <u>7</u>The day of unleavened bread came, on which the Passover must be sacrificed. <u>8</u>He sent Peter and John, saying,</p><h1>JESUS</h1><h2>Go and prepare the Passover for us, that we may eat.</h2><p>   <u>9</u>They said to him,</p><h1>SIMON PETER &amp; JOHN THE DISCIPLE</h1><h3>Where do you want us to prepare?</h3><p>   <u>10</u>He said to them,</p><h1>JESUS</h1><h2>Behold, when you have entered into the city, a man carrying a pitcher of water will meet you. Follow him into the house which he enters. <u>11</u>Tell the master of the house, ‘The Teacher says to you, “Where is the guest room, where I may eat the Passover with my disciples?”’ <u>12</u>He will show you a large, furnished upper room. Make preparations there.</h2><p>   <u>13</u>They went, found things as he had told them, and they prepared the Passover. <u>14</u>When the hour had come, he sat down with the twelve apostles. <u>15</u>He said to them,</p><h1>JESUS</h1><h2>I have earnestly desired to eat this Passover with you before I suffer, <u>16</u>for I tell you, I will no longer by any means eat of it until it is fulfilled in God’s Kingdom.</h2><p><u>17</u>He received a cup, and when he had given thanks, he said,</p><h1>JESUS</h1><h2>Take this, and share it among yourselves, <u>18</u>for I tell you, I will not drink at all again from the fruit of the vine, until God’s Kingdom comes.</h2><p>   <u>19</u>He took bread, and when he had given thanks, he broke it, and gave to them, saying,</p><h1>JESUS</h1><h2>This is my body which is given for you. Do this in memory of me.</h2><p><u>20</u>Likewise, he took the cup after supper, saying,</p><h1>JESUS</h1><h2>This cup is the new covenant in my blood, which is poured out for you. <u>21</u>But behold, the hand of him who betrays me is with me on the table. <u>22</u>The Son of Man indeed goes, as it has been determined, but woe to that man through whom he is betrayed!</h2><p>   <u>23</u>They began to question among themselves, which of them it was who would do this thing. <u>24</u>There arose also a contention among them, which of them was considered to be greatest. <u>25</u>He said to them,</p><h1>JESUS</h1><h2>The kings of the nations lord it over them, and those who have authority over them are called ‘benefactors.’ <u>26</u>But not so with you. But one who is the greater among you, let him become as the younger, and one who is governing, as one who serves. <u>27</u>For who is greater, one who sits at the table, or one who serves? Isn’t it he who sits at the table? But I am among you as one who serves. <u>28</u>But you are those who have continued with me in my trials. <u>29</u>I confer on you a kingdom, even as my Father conferred on me, <u>30</u>that you may eat and drink at my table in my Kingdom. You will sit on thrones, judging the twelve tribes of Israel.</h2><p>   <u>31</u>The Lord said,</p><h1>JESUS</h1><h2>Simon, Simon, behold, Satan asked to have you, that he might sift you as wheat, <u>32</u>but I prayed for you, that your faith wouldn’t fail. You, when once you have turned again, establish your brothers.*</h2><p>   <u>33</u>He said to him,</p><h1>SIMON PETER</h1><h3>Lord, I am ready to go with you both to prison and to death!</h3><p>   <u>34</u>He said,</p><h1>JESUS</h1><h2>I tell you, Peter, the rooster will by no means crow today until you deny that you know me three times.</h2><p>   <u>35</u>He said to them,</p><h1>JESUS</h1><h2>When I sent you out without purse, and wallet, and shoes, did you lack anything?</h2><p>   They said,</p><h1>THE DISCIPLES</h1><h3>Nothing.</h3><p>   <u>36</u>Then he said to them,</p><h1>JESUS</h1><h2>But now, whoever has a purse, let him take it, and likewise a wallet. Whoever has none, let him sell his cloak, and buy a sword. <u>37</u>For I tell you that this which is written must still be fulfilled in me: ‘He was counted with transgressors.’* For that which concerns me has an end.</h2><p>   <u>38</u>They said,</p><h1>THE DISCIPLES</h1><h3>Lord, behold, here are two swords.</h3><p>   He said to them,</p><h1>JESUS</h1><h2>That is enough.</h2><p>   <u>39</u>He came out, and went, as his custom was, to the Mount of Olives. His disciples also followed him. <u>40</u>When he was at the place, he said to them,</p><h1>JESUS</h1><h2>Pray that you don’t enter into temptation.</h2><p>   <u>41</u>He was withdrawn from them about a stone’s throw, and he knelt down and prayed, <u>42</u>saying,</p><h1>JESUS</h1><h2>Father, if you are willing, remove this cup from me. Nevertheless, not my will, but yours, be done.</h2><p>   <u>43</u>An angel from heaven appeared to him, strengthening him. <u>44</u>Being in agony he prayed more earnestly. His sweat became like great drops of blood falling down on the ground.<br />   <u>45</u>When he rose up from his prayer, he came to the disciples, and found them sleeping because of grief, <u>46</u>and said to them,</p><h1>JESUS</h1><h2>Why do you sleep? Rise and pray that you may not enter into temptation.</h2><p>   <u>47</u>While he was still speaking, behold, a multitude, and he who was called Judas, one of the twelve, was leading them. He came near to Jesus to kiss him. <u>48</u>But Jesus said to him,</p><h1>JESUS</h1><h2>Judas, do you betray the Son of Man with a kiss?</h2><p>   <u>49</u>When those who were around him saw what was about to happen, they said to him,</p><h1>ELEVEN DISCIPLES</h1><h3>Lord, shall we strike with the sword?</h3><p><u>50</u>A certain one of them struck the servant of the high priest, and cut off his right ear.<br />   <u>51</u>But Jesus answered,</p><h1>JESUS</h1><h2>Let me at least do this</h2><p>—and he touched his ear, and healed him. <u>52</u>Jesus said to the chief priests, captains of the temple, and elders, who had come against him,</p><h1>JESUS</h1><h2>Have you come out as against a robber, with swords and clubs? <u>53</u>When I was with you in the temple daily, you didn’t stretch out your hands against me. But this is your hour, and the power of darkness.</h2><p>   <u>54</u>They seized him, and led him away, and brought him into the high priest’s house. But Peter followed from a distance. <u>55</u>When they had kindled a fire in the middle of the courtyard, and had sat down together, Peter sat among them. <u>56</u>A certain servant girl saw him as he sat in the light, and looking intently at him, said,</p><h1>A SERVANT GIRL</h1><h4>This man also was with him.</h4><p>   <u>57</u>He denied Jesus, saying,</p><h1>SIMON PETER</h1><h3>Woman, I don’t know him.</h3><p>   <u>58</u>After a little while someone else saw him, and said,</p><h1>BYSTANDERS AT THE TRIAL</h1><h4>You also are one of them!</h4><p>   But Peter answered,</p><h1>SIMON PETER</h1><h3>Man, I am not!</h3><p>   <u>59</u>After about one hour passed, another confidently affirmed, saying,</p><h1>BYSTANDERS AT THE TRIAL</h1><h4>Truly this man also was with him, for he is a Galilean!</h4><p>   <u>60</u>But Peter said,</p><h1>SIMON PETER</h1><h3>Man, I don’t know what you are talking about!</h3><p>Immediately, while he was still speaking, a rooster crowed. <u>61</u>The Lord turned, and looked at Peter. Then Peter remembered the Lord’s word, how he said to him, “Before the rooster crows you will deny me three times.” <u>62</u>He went out, and wept bitterly.<br />   <u>63</u>The men who held Jesus mocked him and beat him. <u>64</u>Having blindfolded him, they struck him on the face and asked him,</p><h1>THE TEMPLE GUARDS</h1><h4>Prophesy! Who is the one who struck you?</h4><p><u>65</u>They spoke many other things against him, insulting him.<br />   <u>66</u>As soon as it was day, the assembly of the elders of the people was gathered together, both chief priests and scribes, and they led him away into their council, saying,</p><h1>THE JEWISH COUNCIL</h1><h4><u>67</u>If you are the Christ, tell us.</h4><p>   But he said to them,</p><h1>JESUS</h1><h2>If I tell you, you won’t believe, <u>68</u>and if I ask, you will in no way answer me or let me go. <u>69</u>From now on, the Son of Man will be seated at the right hand of the power of God.</h2><p>   <u>70</u>They all said,</p><h1>THE JEWISH COUNCIL</h1><h4>Are you then the Son of God?</h4><p>   He said to them,</p><h1>JESUS</h1><h2>You say it, because I am.</h2><p>   <u>71</u>They said,</p><h1>THE JEWISH COUNCIL</h1><h4>Why do we need any more witness? For we ourselves have heard from his own mouth!</h4><h5>*Luke 22:32 The word for “brothers” here may be also correctly translated “brothers and sisters” or “siblings.”<br />*Luke 22:37 Isaiah 53:12</h5>","path":"/337","Question1":"In this passage is there an action that you should follow? What is it?","Question2":"What decisions can you make this week to do this?","Question3":"In this passage is there an action from which you should repent? What is it?","Question4":"What decisions can you make this week to do this?","prevPath":"/336","nextPath":"/338"}},"pathContext":{}}

